<template>
  <div class="newslist">
    <el-table
      :data="tableData"
      border
    >
      <el-table-column
        prop="img"
        label="头图"
        width="200">
        <template slot-scope="scope">
          <img :src="scope.row.img" class="img-item">
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label="新闻标题"
      >
      </el-table-column>
      <el-table-column
        prop="id"
        label="id"
      >
      </el-table-column>
      <el-table-column
        label="操作"
      >
        <template slot-scope="scope">
          <el-button  size="small" type="primary" @click="edit(scope.row._id)">编辑</el-button>
          <el-button size="small" type="danger" @click="del(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: "list",
    data() {
      return {
        tableData: []
      }
    },
    methods:{
      edit(id){
        this.$router.push({path:'/projects/update',query:{id}})
      },
      getData(){
        this.$axios.get('getProjects').then(res =>{
          console.log(res.data);
          this.tableData = res.data
        })
      },
      del(val){

        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.post('delProjects',{id: val}).then(res =>{
            console.log(res);
            console.log(val);
            if(res.code == 200){
              this.$message({
                type: 'info',
                message: '已成功删除'
              });
              setTimeout(() =>{
                this.$router.go(0)
              },1500)
            }
          })
        }).catch((err) => {
          console.log(err);
        });


      }
    },
    created(){
      this.getData()
    }
  }
</script>

<style scoped>
  .newslist {
    margin-top: 100px;
    margin-left: 210px;
  }
  .img-item{
    width: 100px;
    height: 100px;
  }
</style>



